.page-registeration .page-register {
    padding-top: 54px;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}
.page-register {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: reverse;
    -ms-flex-direction: row-reverse;
    flex-direction: row-reverse;
    max-width: 1440px;
    margin: 0 auto;
}
.page-registeration .the-form {
    background-color: #fff !important;
    -webkit-box-shadow: 0 0 0.25rem 0 rgb(0 0 0 / 10%);
    box-shadow: 0 0 0.25rem 0 rgb(0 0 0 / 10%);
    color: #383838 !important;
}
.page-register .the-form {
    /*-ms-flex-preferred-size: 50%;*/
    flex-basis: 50%;
    background-color: #515050;
    border-radius: 8px;
    color: #fff;
    padding: 0 42px;
    max-width: 416px;
    position: relative;
}
.page-register .the-form-title {
    font-size: 24px;
    font-weight: 600;
    line-height: 37.544px;
    text-align: center;
    padding: 36px 0;
}
.page-register .the-form .input-group {
    margin-bottom: 16px;
    /*padding: 0 66px 0 44px;*/
}
