    background-color: #fff;
    border: .0625rem solid #dedcdc;
    border-radius: 4px;
    color: #383838;
    font-size: 14px;
    line-height: 19.992px;
    padding: 8px 12px;
    width: 390px;
}
.page-register .the-form .text-input:focus, :focus-visible {
    outline: #cc4e41 auto 1px;
}
