    display: block;
    text-align: center;
    font-size: 12px;
    margin-bottom: 6px;
}
.page-register .the-form .submit-button {
    border-radius: 24px;
    border-width: 0;
    background: #cc4e41;
    color: #fff;
    display: block;
    font-size: 16px;
    line-height: 18.272px;
    padding: 16px 48px;
    margin: 20px auto 42px;
}
.page-register .the-form .disclaimers {
    display: block;
    font-size: 13px;
    line-height: 16.25px;
    text-align: center;
    margin-bottom: 13px;
}
.page-register .the-form .disclaimers a {
    color: #979797;
}
.page-register .the-form .sso-message {
    text-align: center;
    line-height: 20px;
    position: absolute;
    bottom: 10px;
    z-index: -10;
    display: block;
    -webkit-transform: scale(1) translateY(-25%) translateX(0);
    -ms-transform: scale(1) translateY(-25%) translateX(0);
    transform: scale(1) translateY(-25%) translateX(0);
    -webkit-transform-origin: bottom center;
    -ms-transform-origin: bottom center;
    transform-origin: bottom center;
    opacity: 0;
    -webkit-transition: opacity 750ms,-webkit-transform 250ms;
    transition: opacity 750ms,-webkit-transform 250ms;
    -o-transition: transform 250ms,opacity 750ms;
    transition: transform 250ms,opacity 750ms;
    transition: transform 250ms,opacity 750ms,-webkit-transform 250ms;
}
.page-register .the-form .sso-message .email-domain {
    font-weight: 700;
}
.page-register .the-form .sso-message .cta {
    border-radius: 24px;
    padding: 24px 9px;
    margin: 15px 0;
    text-decoration: none;
    border: 2px solid #cc4e41;
    background-color: #fff;
    color: #cc4e41;
    display: inline-block;
    font-size: 14px;
}
.page-register .customer-list {
