input[type=checkbox] {
  cursor: pointer;
}

.custom-checkbox:checked:before {
  content: '\2714';
  background: #353C4E;
  display: inline-block;
  width: 16px;
  height: 16px;
  color: #f3f5f8;

}

.custom-checkbox:before {
  content: "\202A";
  background: #f3f5f8;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, .2);
  font-size: 20px;
  text-align: center;
  line-height: 8px;
  display: inline-block;
  width: 16px;
  height: 16px;
  color: #00904f;
  border: 1px solid #cdcdcd;
  border-radius: 1px;
  position: relative;
  left: 0;
  top: 2px;
}

.custom-checkbox-qs:checked:before {
  content: '\2714';
  background: #353C4E;
  display: inline-block;
  width: 16px;
  height: 16px;
  color: white;

}
.custom-checkbox-qs:before {
  content: "\202A";
  /*background: #f3f5f8;*/
  text-shadow: 1px 1px 1px rgba(0, 0, 0, .2);
  font-size: 20px;
  text-align: center;
  line-height: 8px;
  display: inline-block;
  width: 16px;
  height: 16px;
  color: #00904f;
  /*border: 1px solid #cdcdcd;*/
  border-radius: 1px;
  position: relative;
  left: 0;
  top: 0px;
}
